import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Switch  } from 'react-router-dom';
import ReactPixel from 'react-facebook-pixel';
import TagManager from 'react-gtm-module';

// Pages
import HomePage from './pages/HomePage';
import TeamPage from './pages/TeamPage';
// import WhatWhyPage from './pages/WhatWhyPage';
// import RoadmapPage from './pages/RoadmapPage';
// import FAQPage from './pages/FAQPage';
// import BlogPage from './pages/BlogPage';

// General/Home modules
import Top from './components/Top';
import Footer from './components/Footer';

import './App.css';

// FB Pixel implementation + pageView
ReactPixel.init('2357465807812225');
ReactPixel.pageView();

 // GoogletagManager implementation
const tagManagerArgs = {
    gtmId: 'GTM-N64P95M',
    events: { sendUserInfo: 'userInfo' }
};
TagManager.initialize(tagManagerArgs);



class App extends Component {
  render() {
    return (
        <Router>
            <div className="App">
                <Top hasBg={true} />

                <Switch>
                    <Route exact path={`${process.env.PUBLIC_URL}/`} component={HomePage} />
                    <Route path={`${process.env.PUBLIC_URL}/team`} component={TeamPage} />
                </Switch>

                <Footer />
            </div>
        </Router>
    );
  }
}

export default App;



